<template>
    <div>
      <common-header :title="titlename"></common-header> 
      <div class="laout_fdl" @click="detailLeft($event)">左</div> 
      <div class="laout_fdr" @click="detailRight($event)">右</div>
      <div class="content" v-show="this.resumedata">
          <div class="topinfo" v-if="this.resumedata.resume">
              <dl>
                  <dt><img :src="'http://campusimage.zhaopin.com/pic/'+this.resumedata.resume.photo_attach.work_photo_path.Text" width="120" height="100"/></dt>
                  <dd >
                  <span v-if="checkisnull(this.resumedata.resume.person_info)">{{this.resumedata.resume.person_info.name.Text}}</span>
                  <span v-if="checkisnull(this.resumedata.resume.person_info)">{{this.resumedata.resume.person_info.gender.Text}}</span>
                  <span v-if="checkisnull(this.resumedata.resume.education_experience[0])">{{this.resumedata.resume.education_experience[0].education_degree.Text}}</span>
                  <span v-if="checkisnull(this.resumedata.resume.education_experience[0])">{{this.resumedata.resume.education_experience[0].edu_type.Text}}</span></dd>
                  <dd>
                    <span v-if="checkisnull(this.resumedata.resume.education_experience[0])">{{this.resumedata.resume.education_experience[0].school_name.Text}}</span>
                    <span v-show="checkisnull(this.resumedata.resume.education_experience[0].school_name.Text)&&checkisnull(this.resumedata.resume.education_experience[0].major_name.Text)">|</span>
                    <span v-if="checkisnull(this.resumedata.resume.education_experience[0])">{{this.resumedata.resume.education_experience[0].major_name.Text}}</span>
                  </dd>
              </dl>
          </div>
          <div class="examinfo" v-if="this.examdata">
                <div class="fdiv"><span>考场安排</span></div>
                <div class="divcon">
                    <div v-show="this.examdata.Position">投递岗位：{{this.examdata.Position}}</div>
                    <div v-show="this.examdata.Address">所在考场：{{this.examdata.Address}} <span v-show="this.examdata.SeatNo">{{this.examdata.SeatNo}} 号</span></div>
                    <div v-show="this.examdata.StartDate">面试时间：{{this.examdata.StartDate}}</div>
                </div>
          </div>
           <div class="resumeinfo"  v-if="checkisnull(this.resumeConfig)&&this.resumedata.resume">
            <div v-for="(item,index) in this.resumeConfig">
                <div class="fdiv" v-if="item.caption&&item.caption!=='附件'&&checkmodelisnull(resumedata.resume[item.field_name])"><span v-if="item.caption==='个人信息'">简历信息</span><span v-else>{{item.caption}}</span></div>
                <div class="divcon" v-for="(obj,key) in item.item" v-if="isshowrow(obj)&&!Array.isArray(resumedata.resume[item.field_name])" >
                     <div v-if="obj.field_name==='name'" class="urseName">
                        {{resumedata.resume[item.field_name][obj.field_name].Text}}
                      </div>
                      <div v-else-if="obj.field_name==='gender'&&resumedata.resume[item.field_name][obj.field_name]&&resumedata.resume[item.field_name][obj.field_name].Text" class="cnamestyle clearfix">
                        <span>{{getDealWithField(resumedata.resume[item.field_name]['gender'])}}</span>
                        <span v-show="!!resumedata.resume[item.field_name]['nowlocated_city']&&!!resumedata.resume[item.field_name]['birth'].Text">|</span>
                        <span>{{getAges(getDealWithField(resumedata.resume[item.field_name]['birth']))}}</span>
                        <span v-show="!!resumedata.resume[item.field_name]['nowlocated_city']&&!!resumedata.resume[item.field_name]['nowlocated_city'].Text">|</span>
                        <span>{{getDealWithField(resumedata.resume[item.field_name]['nowlocated_city'])}}</span>
                      </div>
                     <div v-else-if="Array.isArray(resumedata.resume[item.field_name][obj.field_name])">
                          {{obj.caption}}：
                        <ul >
                          <li v-for="subItem in resumedata.resume[item.field_name][obj.field_name]">
                            <span v-for="(keys,k,i) in subItem" v-if="keys&&keys.Value">
                              {{getDealWithField(keys)}}<span v-show="islastarray(keys,i,subItem)">|</span>
                            </span>
                          </li>
                        </ul>
                     </div>
                     <div v-else-if="!Array.isArray(resumedata.resume[item.field_name][obj.field_name])&&obj.caption">
                         {{obj.caption}}：{{getDealWithField(resumedata.resume[item.field_name][obj.field_name])}}
                     </div>
                </div>
                <div v-if="resumedata.resume[item.field_name]&&Array.isArray(resumedata.resume[item.field_name])">
                  <div class="divcon" v-for="(objex,ox) in resumedata.resume[item.field_name]" v-if="objex[item.item[0].field_name]&&checkarrayisnull(objex)">
                      <div v-for="(obje,oe) in item.item" v-if="objex[obje.field_name]">
                        <div v-if="obje.caption&&objex[obje.field_name]&&objex[obje.field_name].Text">
                         {{obje.caption}}：{{getDealWithField(objex[obje.field_name])}}
                         </div>
                     </div>
                  </div>
                </div>
            </div>
          </div>
      </div> 
      <btn-footer :state="currentstate"></btn-footer> 
    </div>
