        if(!!data&&data.code)
        {
            this.resumedata=!!data.result.ResumeSnapshot?JSON.parse(data.result.ResumeSnapshot):'';
            this.examdata=!!data.result.ExamInfo?data.result.ExamInfo:'';
            console.log(this.resumedata.resume);
            if(this.resumedata.resume)
            {
              localStorage.setItem("resumeinfo",JSON.stringify(this.resumedata.resume));
            }
        }
    },
    checkisnull(data)
    {
       if(data===null||data===undefined||data==='')
       {
         return false;
       }
       return true;
    },
    //檢查當前模块 是否有值  
    checkmodelisnull(currentmodel)
    {
        let bclist=[];
        for(let items in currentmodel){
            if(Array.isArray(currentmodel[items]))
            {
               for(let items in currentmodel[items]){
                for(let keys in items){
                  if(!items,items[keys]||!items,items[keys].Text){
                    bclist.push(false);
                  }else{
                    bclist.push(true);
                  }      
                }
               }
            }else{
              if(!currentmodel[items]||!currentmodel[items].Text){
                bclist.push(false);
              }else{
                bclist.push(true);
              }
            }
        }
        if(bclist.indexOf(true)>-1)
        {
          return true;
        }
        return false;
    },
    //檢查當前array 是否有值  
    checkarrayisnull(currentarray)
    {
        let bclist=[];
        for(let items in currentarray){
            if(!!currentarray[items]){
              if(currentarray[items].Text==null||currentarray[items].Text=='null'||!currentarray[items].Text)
              {
                  bclist.push(false);
              }else{
                  bclist.push(true);
              }
            }
          }
        if(bclist.indexOf(true)>-1)
        {
          return true;
        }
        return false;
    },
    isshowrow(currentobj)
    {
      console.log(currentobj.field_name)
        if(currentobj.caption&&currentobj.caption!=='工作照'&&currentobj.caption!=='简历附件')
        {
          return true
        }
        return false;
    },
    getAges(str) {
      let r = str.match(/^(\d{1,4})(-|\/)(\d{1,2})\2(\d{1,2})$/);
      if (r == null) return '';
      let d = new Date(r[1], r[3] - 1, r[4]);
      let Y;
      if (d.getFullYear() == r[1] && (d.getMonth() + 1) == r[3] && d.getDate() == r[4]) {
        Y = new Date().getFullYear();
      }
      return (Y - r[1]) + '岁';
    },
    isNull(str) {
      if (!str) return true;
      var regu = "^[ ]+$";
      var re = new RegExp(regu);
      return re.test(str);
    },
     getDealWithField(obj) {
      obj = obj || {};
      return this.isNull(obj.Text) ? '' : obj.Text;
    },
