<script>
  import Router from './../router';
  import Tool from '../configs/tool.js';
  export default {
    name: 'DetailFootBtn',
    props:['state'],
    data(){
      return {
        default: true
      }
    },
    methods: {
     setScore(e,state){
         //设置成绩
     },
     showcomment(e)
     {
         //切换路由到评论页
        this.$router.push({
          name: 'ResumeComment',
          query: {
            state: this.state
          }
        });
     }
    },
    created(){
        
    }
  }
</script>

<template>
  <footer class="btn_footer"  >
     <ul>
         <li @click="setScore($event,0)">通过</li>
         <li @click="setScore($event,1)">不通过</li>
         <li @click="setScore($event,2)">待定</li>
         <li @click="showcomment($event)">面试结果</li>
     </ul>
  </footer>
</template>
<style lang="less" scoped>
 @import "../assets/css/detailFootBtn.less";
</style>


